@import url(https://fonts.googleapis.com/css?family=Dosis:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: 'Dosis', sans-serif;
}

* {
  font-family: 'Dosis', sans-serif;
}
*{box-sizing:border-box}h2{text-align:center}.App{background-image:url(/static/media/bg.93e18e10.svg);background-size:cover;min-height:100vh}.room{width:90%;margin:0 auto;padding-top:50px;display:grid;grid-template-columns:3fr 6fr}.room .form{display:grid;border:3px solid black;border-right:none;border-radius:10px;margin:0 auto 30px;grid-template-columns:100px 1fr 120px;-webkit-transition:0.4s ease-in-out;transition:0.4s ease-in-out;box-shadow:15px 15px 15px rgba(0,0,0,0.671)}.room .form:hover{box-shadow:5px 5px 5px rgba(0,0,0,0.671)}.room .form h3{margin:0;display:flex;align-items:center;text-align:center;background:black;color:white}.room .form input{font-size:35px;border:none;padding:10px 7px}.room .form button{background:none;background:white;color:black;font-size:20px;border:none;border-radius:0 10px 10px 0;border-left:1px solid black;-webkit-transition:0.1s ease-in-out;transition:0.1s ease-in-out}.room .form button:hover{background:black;border-left:1px solid white;color:white}#yt-embedded-player{grid-column:span 9;width:100%;min-height:80vh;margin:auto;box-shadow:25px 25px 25px black;-webkit-transition:0.4s ease-in-out;transition:0.4s ease-in-out}#yt-embedded-player:hover{box-shadow:11px 11px 11px black}.controls{grid-column:span 9}

